pre.prettyprint {
    border: 1px solid #d6d6d6;
    background-color: #eee;
    width: 98%;
    border-radius: 0;
    padding: 5px;
    margin: 0;
    margin-bottom: 10px;
    margin-top: 15px;
    font-size: 13px;
    overflow: auto;
}
pre.prettyprint {
    line-height: 16px;
}
pre.result, pre.prettyprint {
    width: 100%!important;
    // border-radius: 0;
}
.pun, .opn, .clo {
    color: #660;
}
.typ {
    color: #7f0055;
}
.lit {
    color: #066;
}
.str {
    color: #080;
}
.kwd {
    color: #008;
}